import { renderHook } from '@testing-library/react-native';
import { Animated } from 'react-native';
import useProgressAnimation from '../../src/components/ui/ProgressBar/useProgressAnimation';

// Mock Animated.timing
jest.mock('react-native', () => ({
  Animated: {
    Value: jest.fn().mockImplementation(() => ({
      setValue: jest.fn(),
      interpolate: jest.fn().mockReturnValue('50%'),
    })),
    timing: jest.fn().mockReturnValue({
      start: jest.fn(),
    }),
  },
}));

describe('useProgressAnimation', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('should create animated progress when animated is true', () => {
    const { result } = renderHook(() =>
      useProgressAnimation({ progress: 50, animated: true, duration: 600 })
    );

    expect(Animated.timing).toHaveBeenCalledWith(
      expect.any(Object),
      expect.objectContaining({
        toValue: 50,
        duration: 600,
        useNativeDriver: false,
      })
    );
    expect(result.current.width).toBe('50%');
  });

  it('should set progress directly when animated is false', () => {
    const mockSetValue = jest.fn();
    const mockAnimatedValue = { setValue: mockSetValue, interpolate: jest.fn().mockReturnValue('75%') };
    
    // Mock Animated.Value pour retourner notre valeur mockÃ©e
    Animated.Value.mockReturnValue(mockAnimatedValue);

    const { result } = renderHook(() =>
      useProgressAnimation({ progress: 75, animated: false, duration: 600 })
    );

    expect(mockSetValue).toHaveBeenCalledWith(75);
    expect(Animated.timing).not.toHaveBeenCalled();
    expect(result.current.width).toBe('75%');
  });

  it('should clamp progress values between 0 and 100', () => {
    const { result } = renderHook(() =>
      useProgressAnimation({ progress: 150, animated: true })
    );

    expect(Animated.timing).toHaveBeenCalledWith(
      expect.any(Object),
      expect.objectContaining({
        toValue: 100,
      })
    );
  });

  it('should handle negative progress values', () => {
    const { result } = renderHook(() =>
      useProgressAnimation({ progress: -25, animated: true })
    );

    expect(Animated.timing).toHaveBeenCalledWith(
      expect.any(Object),
      expect.objectContaining({
        toValue: 0,
      })
    );
  });

  it('should use default duration when not provided', () => {
    const { result } = renderHook(() =>
      useProgressAnimation({ progress: 50, animated: true })
    );

    expect(Animated.timing).toHaveBeenCalledWith(
      expect.any(Object),
      expect.objectContaining({
        duration: 600,
      })
    );
  });

  it('should return width interpolation', () => {
    const { result } = renderHook(() =>
      useProgressAnimation({ progress: 50, animated: true })
    );

    expect(result.current).toHaveProperty('width');
    expect(typeof result.current.width).toBe('string');
  });
});
